import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ListeAnnoncesComponent } from './liste-annonces/liste-annonces.component';

import { AnnoncesComponent } from './annonces.component';
import { AnnoncesService } from './annonces.service';
import { DetailAnnonceComponent } from './detail-annonce/detail-annonce.component';
import { AnnonceFormComponent } from './annonce-form/annonce-form.component';

const routes: Routes = [
  {path:'',
  component:AnnoncesComponent,
  },
{
  path: 'all',
  component: ListeAnnoncesComponent
  // resolve: AnnoncesService
},
{
  path: 'annonce/:idAnnonce',
  component: DetailAnnonceComponent
  // resolve: AnnoncesService
},
{
  path: 'detail',
  component: DetailAnnonceComponent
  // resolve: AnnoncesService
},
{
  path: 'create',
  component: AnnonceFormComponent
  // resolve: AnnoncesService
}
  ];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AnnoncesRoutingModule { }
